@import url('https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

body {
  margin: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
   font-family: 'Lato', sans-serif; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card-container {
	width: 100%;
	margin: 0 auto;
	font-weight: 300;
}

.game-card {
	background-color: #e95a4b;
	color: white;
	width: 25%;
	height: auto;
}

.game-name {
	width: 100%;
	height: 75px;
	border: none;
	text-transform: uppercase;
	font-size: 30px;
	letter-spacing: 2px;
}

.game-separator { background-color: #fff; height: 2px; margin: 10px auto 30px auto;}

.game-rank, .game-platform, .game-genre, .game-publisher, .rank-sales, .game-sales { height: 50px; font-weight: 300; }

.search-box {
	background-color: transparent;
}

.search-box input[type=search] {
	padding: 10px;
	background-color: #fff;
	color: #000;
	border: 2px solid #555;
	border-radius: 10px;
	text-align: center;
	width: 50%;
	outline: none;
	margin: 0 auto 30px auto;
}

.searchbar-header { color: #000; padding-top: 50px; }

